import React from 'react'
import { NavLink } from 'react-router'

function Navbar() {
  return (
    <div>
       <header className="header-area header-sticky">
    <div className="container">
        <div className="row">
            <div className="col-12">
                <nav className="main-nav">
                   {/* <!-- ***** Logo Start ***** -->*/}
                    <NavLink to="/" className="logo">
                        <img src="assets/images/logo.png" alt=""/>
                    </NavLink>
                  {/* <!-- ***** Logo End ***** -->*/}
                   {/* <!-- ***** Menu Start ***** -->*/}
                    <ul className="nav">
                        <li><NavLink to="/" >Home</NavLink></li>
                        <li><NavLink to="/explor">Explore</NavLink></li>
                        <li><NavLink to="/detailes">Item Details</NavLink></li>
                        <li><NavLink to="/auther">Author</NavLink></li>
                        <li><NavLink to="/create">Create Yours</NavLink></li>
                    </ul>   
                    <a   className="menu-trigger">
                        <span>Menu</span>
                    </a>
                   {/* <!-- ***** Menu End ***** -->*/}
                </nav>
            </div>
        </div>
    </div>
  </header>
    </div>
  )
}

export default Navbar
